* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.wrapper {
  width: 90vw;
  height: 99vh;
  margin: 0 auto; }

.wrapper {
  display: grid;
  grid-gap: 1px;
  grid-template-rows: 80px 40px auto 80px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: "main-header main-header main-header main-header" "main-nav-left main-nav-right main-nav-right main-nav-right" "left-aside main-container main-container main-container" "footer footer footer footer"; }

@media only screen and (max-width: 640px) {
  .wrapper {
    grid-template-rows: 2fr 1fr 1fr 2fr auto 2fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-areas: "main-header" "main-nav-right" "main-nav-left" "left-aside" "main-container" "footer"; } }

.wrapper > div {
  border: 1px solid #e9ab58;
  border-radius: 2px;
  background-color: rgba(233, 171, 88, 0.5);
  padding: 1em;
  color: #d9480f; }

.main-header {
  grid-area: main-header; }

.main-nav-left {
  grid-area: main-nav-left; }

.main-nav-right {
  grid-area: main-nav-right; }
  .main-nav-right ul {
    display: flex;
    justify-content: space-evenly; }
    .main-nav-right ul li {
      display: inline-block;
      padding: 2px 5px 2px 5px;
      text-align: center;
      width: 100%; }
      .main-nav-right ul li:hover {
        cursor: pointer;
        background-color: coral;
        color: white; }

.left-aside {
  grid-area: left-aside; }

.main-container {
  grid-area: main-container; }

.footer {
  grid-area: footer; }
